import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatInputModule, MatButtonModule, MatSelectModule, 
    MatIconModule, MatMenuModule, MatRadioModule, 
    MatDialogModule, MatProgressBarModule, MatSliderModule,
    MatToolbarModule, MatCardModule } from '@angular/material';
import { BaseRequestOptions } from '@angular/http';
import { Angular2FontawesomeModule } from 'angular2-fontawesome/angular2-fontawesome';
import {FlexLayoutModule} from "@angular/flex-layout";
import { SortablejsModule } from 'angular-sortablejs';

import { routing } from './app.routing';
import { AuthGuard } from './_guards/index';

import { AuthenticationService, HttpService } from './_services/index';

import { AppComponent } from './_components/app.component';
import { WelcomeComponent } from './_components/welcome/index';
import { LoginComponent } from './_components/welcome/login/index';
import { RegisterComponent } from './_components/welcome/register/index';
import { TryComponent } from './_components/welcome/try/try.component';
import { HomeComponent } from './_components/home/index';
import { TopicComponent } from './_components/home/topic/index';
import { LessonComponent, GoodDialogComponent, BadDialogComponent, 
    ReportDialogComponent, ChartComponent } 
    from './_components/home/topic/lesson/index';
import { ProfileComponent } from './_components/profile/profile.component';

@NgModule({
    imports: [
        BrowserModule,
        FormsModule,
        HttpModule,
        routing,
        Angular2FontawesomeModule,
        BrowserAnimationsModule,
        MatInputModule, 
        MatButtonModule,
        MatSelectModule,
        MatIconModule,
        MatMenuModule,
        MatRadioModule,
        MatDialogModule,
        MatProgressBarModule,
        MatSliderModule,
        MatToolbarModule, 
        MatCardModule,
        FlexLayoutModule,
        SortablejsModule.forRoot({ animation: 150 })
    ],
    declarations: [
        AppComponent,
        WelcomeComponent,
        LoginComponent,
        RegisterComponent,
        HomeComponent,
        TopicComponent,
        LessonComponent,
        GoodDialogComponent,
        BadDialogComponent,
        ReportDialogComponent,
        ChartComponent,
        ProfileComponent,
        TryComponent
    ],
    entryComponents: [
        GoodDialogComponent,
        BadDialogComponent,
        ReportDialogComponent
    ],
    providers: [
        AuthGuard,
        AuthenticationService,
        HttpService,

        // providers used to create fake backend
        //fakeBackendProvider,
        //MockBackend,
        BaseRequestOptions
    ],
    bootstrap: [AppComponent]
})

export class AppModule { }


